  <div class="bg-gradient-primary">
    <div class="container">
      <!-- Outer Row -->
      <div class="row justify-content-center">
        <div class="col-xl-10 col-lg-12 col-md-9">
          <div class="card o-hidden border-0 shadow-lg my-5">
            <div class="card-body p-0">
              <!-- Nested Row within Card Body -->
              <div class="row">
                <div class="col-lg-6 d-none d-lg-block bg-login-image"></div>
                <div class="col-lg-6">
                  <div class="p-5">
                    <div class="text-center">
                      <h1 class="h4 text-gray-900 mb-4">Registre-se</h1>
                    </div>
                    <form class="user" @submit.prevent="signup" id="login-form">
                      <div class="form-group">
                        <input
                          type="email"
                          v-model="ViewModel.email"
                          id="email"
                          class="form-control form-control-user"
                          aria-describedby="emailHelp"
                          placeholder="E-mail"
                          required
                        />
                        <!-- id="exampleInputEmail" -->
                      </div>
                      <div class="form-group">
                        <input
                          type="password"
                          class="form-control form-control-user"
                          placeholder="Senha"
                          v-model="ViewModel.password"
                          id="password"
                          required
                        />
                      </div>
                      <div class="form-group">
                        <input
                          type="password"
                          class="form-control form-control-user"
                          placeholder="Confirmação de senha"
                          v-model="ViewModel.passwordConfirmation"
                          id="password"
                          required
                        />
                      </div>

                      <button
                        type="submit"
                        class="btn btn-primary btn-user btn-block"
                      >
                        <span v-if="!xhrRequest">Registrar</span>
                        <span v-if="xhrRequest">Aguarde...</span>
                        <div
                          v-if="xhrRequest"
                          class="spinner-border text-secondary _loader"
                          role="status"
                        >
                          <span class="sr-only">Loading...</span>
                        </div>
                      </button>
                      <hr />
                      <!-- <a
                        href="index.html"
                        class="btn btn-google btn-user btn-block"
                      >
                        <i class="fab fa-google fa-fw"></i> Login with Google
                      </a>
                      <a
                        href="index.html"
                        class="btn btn-facebook btn-user btn-block"
                      >
                        <i class="fab fa-facebook-f fa-fw"></i> Login with
                        Facebook
                      </a> -->
                    </form>
                    <!-- <hr /> -->
                    <div class="col-sm-12 text-center form-group mt-5">
                      <p>
                        Já tem uma conta?
                        <router-link to="/login">Login</router-link>
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div v-if="errorMessage !== ''" class="alert alert-danger" role="alert">
        {{ errorMessage }}
      </div>
      <div
        v-if="successMessage !== ''"
        class="alert alert-success"
        role="alert"
      >
        {{ successMessage }}
      </div>
    </div>
  </div>
</template>
<script>
import firebase from "firebase";
import { ref } from "vue";
import Swal from "sweetalert2";
import { useRouter } from "vue-router";
export default {
  name: "Signup",
  data() {
    return {
      email: "",
      password: "",
      xhrRequest: false,
      errorMessage: "",
      successMessage: "",
    };
  },
  setup() {
    let router = useRouter();
    let ViewModel = ref({
      email: "",
      password: "",
      passwordConfirmation: "",
    });
    const signup = () => {
      if (ViewModel.value.password != ViewModel.value.passwordConfirmation) {
        console.log("As senhas não conferem");
      } else if (ViewModel.value.password.length < 6) {
        console.log("A senhas deve ter no minimo 6 caracteres");
      } else {
        firebase
          .auth()
          .createUserWithEmailAndPassword(
            ViewModel.value.email,
            ViewModel.value.password
          )
          .then(
            () => {
              //   v.successMessage = "Register Successfully.";
              //   v.xhrRequest = false;
              Swal.fire({
                icon: "success",
                title: `Sucesso`,
                text: `O e-mail "${ViewModel.value.email}" foi cadastre-se com sucesso`,
                confirmButtonText: `Entrar`,
              }).then((response) => {
                if (response.isConfirmed) {
                  firebase
                    .auth()
                    .signInWithEmailAndPassword(
                      ViewModel.value.email,
                      ViewModel.value.password
                    )
                    .then(
                      () => {
                        router.push("/dashboard");
                      },
                      (error) => {
                        console.log("erro", error.message);
                      }
                    );
                }
              });
              console.log("Register Successfully");
            },
            (error) => {
              //   let errorResponse = JSON.parse(error.message);
              //   v.errorMessage = errorResponse.error.message;
              //   v.xhrRequest = false;
              Swal.fire({
                icon: "warning",
                title: `Atenção`,
                text: `${error.message}`,
                confirmButtonText: `Fechar`,
              });
              console.log("erro", error.message);
            }
          );
      }
    };
    return {
      ViewModel,
      signup,
    };
  },
  methods: {
    signupRequest() {
      let v = this;
      v.xhrRequest = true;
      v.errorMessage = "";
      v.successMessage = "";

      firebase
        .auth()
        .createUserWithEmailAndPassword(v.email, v.password)
        .then(
          () => {
            v.successMessage = "Register Successfully.";
            v.xhrRequest = false;
          },
          (error) => {
            let errorResponse = JSON.parse(error.message);
            v.errorMessage = errorResponse.error.message;
            v.xhrRequest = false;
          }
        );
    },
  },
};
</script>
<style scoped>
._loader {
  position: relative;
  top: 6px;
  left: 10px;
}
</style>